:root
{
  --dark-color: rgb(2, 172, 172);
  --light-color: rgb(140, 248, 248);
}
body {
  margin: 0;
  font-family: '源ノ角ゴシック', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face
{
  font-family: 'GL-MahjongTile-Clr';
  src: url('/GL-MahjongTile-Clr.woff2') format('woff2');
}

div.tile.pm
{
  position: relative;
  overflow: hidden;
  width: 64px;
  height: 88px;
  display: inline-block;
}
div.tile.gl
{
  position: relative;
  width: 64px;
  height: 91px;
  display: inline-block;
}
div.tile > img
{
  position: absolute;
  object-fit: contain;
}
.start-button, .hu-button
{
  border: 1px solid var(--dark-color);
  color: var(--dark-color);
  font-weight: bold;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24em;
}
.hu-button
{
  height: 3em;
  margin-bottom: 0.5em;
}
.status-labels
{
  width: 24em;
  display: block;
}
.status-label
{
  margin: 0;
  width: 12em;
  display: inline-block;
}

fieldset
{
  margin-top: 0.5ex;
  margin-bottom: 0.5ex;
  margin-inline-start: 0;
  margin-inline-end: 0;
  border: none;
  padding-block-start: 0;
  padding-block-end: 0;
  padding-inline-start: 0;
  padding-inline-end: 0;
}
.transparent
{
  opacity: 0;
}
.radio-label
{
  display: inline-block;
  box-sizing: border-box;
  padding-left: 0.4em;
  width: 6em;
}
fieldset > label
{
  display: inline-block;
}
fieldset.hand-length > label
{
  width: 3em;
}
fieldset.tile-style > label
{
  width: 9em;
}
fieldset.tile-suit > label,
fieldset.allow-pairs > label
{
  width: 6em;
}
label[for=time-before-draw], label[for=time-before-sort], label[for="time-of-game"]
{
  box-sizing: border-box;
  padding-left: 0.4em;
  width: 9em;
}
input[type="range"]
{
  -webkit-appearance: none;
  font-size: 100%;
  height: 0.2em;
  border-radius: 0.1em;
  background-color: var(--light-color);
  width: 12em;
  margin: 0;
  vertical-align: middle;
}
input[type="range"]::-webkit-slider-thumb
{
  -webkit-appearance: none;
  background: var(--dark-color);
  height: 0.6em;
  width: 0.6em;
  border-radius: 50%;
}
input[type="range"] + output
{
  width: 3em;
}
fieldset > input + label
{
  box-sizing: border-box;
  border: 0.5px solid white;
  font-weight: bold;
  text-align: center;
}
fieldset > input:not(:checked) + label
{
  background-color: var(--light-color);
}
fieldset > input:checked + label
{
  background-color: var(--dark-color);
  color: white;
}
fieldset > input[type="radio"]
{
  display: none;
}
